export default {
	data() {
		return {
			isSelect:false,
			currentPage:1, //当前页
			addressList:[],
			moreAddressList:false,//更多地址
			searchKey:'',//地址搜索关键字
		};
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
		this.headerTop = e.scrollTop>=0?null:0;
		this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
	},
	
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		
		let self = this;
		if (self.moreAddressList) {
			uni.showToast({ title: '触发上拉加载' });
			self.currentPage++;
			self.loadData();
			return false;
		}
	},
	async onLoad() {
		//获取收货地址列表
		this.loadData();
	},
	onShow() { //返回显示页面状态函数
		
		//只执行获取地址的方法，来进行局部刷新
		this.loadData();
	},

	methods:{
		 async loadData() {
			let self = this;
			self.$axios(self.$baseUrl.getAdressLists,{
				params:self.searchKey,
				page:self.currentPage
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					self.lastPage = result.last_page;
					self.total = result.total;
					if(self.currentPage < self.lastPage ){
						self.moreAddressList = true;
					}else{
						self.moreAddressList = false;
					}
					if(self.currentPage == 1){
						self.addressList = result.data;
					}else{
						
						result.data.forEach((item)=>{
							self.addressList.push(item) ;
						})
						
					}
				}
				
				
			},(error) =>{
				console.log(error)
			})
			
			
		},
		//收货地址搜索功能
		onSearch(){
			let self = this;
			self.currentPage = 1;
			self.loadData();
		},
		
		//编辑地址
		edit(row){
			let addressId = row.address_id;
			uni.navigateTo({
				url: `edit/edit?id=${addressId}`
			})
			
		},
		//新增地址
		add(){
			uni.navigateTo({
				url:"edit/edit?type=add"
			})
		},
		//选择收货地址
		select(row){
			let self = this;
			let addressId = row.address_id;
			//是否需要返回地址(从订单确认页跳过来选收货地址)
			var pages = getCurrentPages();
			var currPage = pages[pages.length - 1]; //当前页面
			var prevPage = pages[pages.length - 2]; //上一个页面
			//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
			prevPage.setData({
				addressId:addressId
			})
			uni.navigateBack();
			
		}
	}
}
